'use client'
import { useState } from "react";
import { students } from "@/app/demo/list";
const AdminPage = () => {
    // const [admin, setAdmin] = useState('');
    const [admin, setAdmin] = useState<null | typeof students>(null);
    const [error, setError] = useState('');
    const handleAdmin = (e: React.FormEvent) => {
        e.preventDefault();
        const allFound = students.map(std => std)
        if (allFound) {
            setAdmin(allFound)
            setError('');
        }
        else {
            setError('No users found')
        }
    }
    return (
        <>
            <div>
                <h1>Admin dashboard</h1>
                <p>hello word</p>
                <button onClick={handleAdmin}>submit</button>
            {/* </div> */}
            {/* <div> */}
                {error && <p className="text-red-500">{error}</p>}
                {admin && (
                    <div className="ml-30">
                        {
                        admin.map(std => (

                            <p><strong>Name:</strong> {std.name} <strong>Age:</strong>{std.age} <strong>Class:</strong>{std.class} <strong>Id:</strong>{std.id}</p>
                        ))
                        }
                    </div>
                )}
            </div>
        </>
    );
}

export default AdminPage;